import React from "react";
import {
	Stack,
	Heading,
	Text,
	Button,
	Image,
	Center,
	Box,
	Badge,
	IconButton,
	Tooltip,
} from "@chakra-ui/react";
import { Link as RLink, useParams } from "react-router-dom";
import axios from "axios";
import config from "../config";
import Loader from "../components/Loader";
import FilmComponent from "../components/Film";
import { Title } from "react-head-meta";
import { StarIcon } from "@chakra-ui/icons";
import { CheckAuth } from "../hooks/Auth";

function Film() {
	let { filmId } = useParams();
	const [notFound, setNotFound] = React.useState<boolean>(false);
	const [isLoading, setIsLoading] = React.useState<boolean>(true);
	const [film, setFilm] = React.useState<any>(null);

	const auth = CheckAuth();

	const [similars, setSimilars] = React.useState<any>([]);
	const [sequelsAndPrequels, setSequelsAndPrequels] = React.useState<any>([]);

	React.useEffect(() => {
		const getFilm = async () => {
			setSimilars([]);
			setSequelsAndPrequels([]);
			setIsLoading(true);
			try {
				const { data: res } = await axios.get(
					`${config.api_url}/films/${filmId}`,
					{
						headers: {
							"X-API-KEY": config.api_token,
						},
					}
				);

				setFilm(res);
				setNotFound(false);
			} catch {
				setNotFound(true);
			} finally {
				setIsLoading(false);
			}
		};
		getFilm();

		if (!notFound) {
			const additional = async () => {
				try {
					const { data: similarsRes } = await axios.get(
						`${config.api_url}/films/${filmId}/similars`,
						{
							headers: {
								"X-API-KEY": config.api_token,
							},
						}
					);

					setSimilars(similarsRes.items);
				} catch {}

				try {
					const { data: sapRes } = await axios.get(
						`${config.sequels_and_prequels_url}/films/${filmId}/sequels_and_prequels`,
						{
							headers: {
								"X-API-KEY": config.api_token,
							},
						}
					);

					setSequelsAndPrequels(sapRes);
				} catch {}
			};
			additional();
		}
		// eslint-disable-next-line
	}, [filmId]);
	return (
		<>
			{(isLoading && (
				<>
					<Title title="KinoSearch" />
					<Loader />
				</>
			)) || (
				<>
					{(notFound && (
						<Center h="80vh">
							<Title title="Страница не найдена – KinoSearch" />
							<Heading>Страница не найдена</Heading>
						</Center>
					)) || (
						<Stack direction="column" spacing={10}>
							<Title title={`${film.nameRu || film.nameEn} – KinoSearch`} />
							<Stack
								spacing="50px"
								direction={["column", "row"]}
								paddingRight={[0, 10]}
							>
								<Image
									src={film.posterUrl}
									w={["auto", "350px"]}
									borderRadius="20px"
								/>
								<Stack direction="column" spacing="20px">
									<Stack
										direction="row"
										justifyContent="space-between"
										spacing="15px"
									>
										<Heading size="xl" fontWeight="800">
											{film.nameRu || film.nameEn}
										</Heading>
										{film.ratingKinopoisk ||
										film.ratingImdb ||
										film.ratingFilmCritics ? (
											<Tooltip
												label="Рейтинг фильма"
												hasArrow
												bg="#252525"
												color="white"
												placement="top"
											>
												<Center>
													<Badge
														colorScheme="green"
														variant="subtle"
														fontSize="lg"
													>
														{film.ratingKinopoisk ||
															film.ratingImdb ||
															film.ratingFilmCritics}
													</Badge>
												</Center>
											</Tooltip>
										) : null}
									</Stack>
									<Text fontWeight="500" fontSize="lg">
										{film.shortDescription || film.slogan || film.description}
									</Text>
									<Stack direction="row" spacing="10px">
										<Button
											colorScheme="brand"
											variant="outline"
											size="lg"
											borderRadius="30px"
											w="100%"
											as={RLink}
											to={`/watch/${filmId}`}
										>
											Смотреть
										</Button>
										{auth && (
											<Tooltip
												label="Добавить в избранное"
												hasArrow
												bg="#252525"
												color="white"
												placement="top"
											>
												<IconButton
													aria-label="Favorite"
													icon={<StarIcon w={6} h={6} />}
													size="lg"
													//variant="ghost"
												/>
											</Tooltip>
										)}
									</Stack>

									<Heading size="xl" fontWeight="800">
										Информация о фильме
									</Heading>
									<Text fontWeight="500" fontSize="lg">
										Год выпуска: {film.year || "неизвестно"}
										<br />
										Страна: {film.countries[0].country || "неизвестно"}
										<br />
										Жанр: {film.genres[0].genre || "неизвестно"}
									</Text>
								</Stack>
							</Stack>
							{sequelsAndPrequels.length !== 0 && (
								<Box>
									<Heading size="md" fontWeight="700">
										Сиквелы и приквелы{" "}
										<Badge ml="1" fontSize="md">
											{sequelsAndPrequels.length}
										</Badge>
									</Heading>
									<Box paddingTop={5}>
										<Stack
											direction="row"
											overflow="auto"
											overflowX="auto"
											spacing="20px"
										>
											{sequelsAndPrequels.map((film: any) => (
												<FilmComponent film={film} />
											))}
										</Stack>
									</Box>
								</Box>
							)}
							{similars.length !== 0 && (
								<Box>
									<Heading size="md" fontWeight="700">
										Если вам понравился этот фильм{" "}
										<Badge ml="1" fontSize="md">
											{similars.length}
										</Badge>
									</Heading>
									<Box paddingTop={5}>
										<Stack
											direction="row"
											overflow="auto"
											overflowX="auto"
											spacing="20px"
										>
											{similars.map((film: any) => (
												<FilmComponent film={film} />
											))}
										</Stack>
									</Box>
								</Box>
							)}
						</Stack>
					)}
				</>
			)}
		</>
	);
}

export default Film;
